h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

.is-hidden {
  display: none;
}

@media only screen and (max-width: 1199px) {
  .desk-only {
    display: none;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

button {
  background-color: transparent;
}

a:hover, a:focus {
  text-decoration: none;
}

a {
  color: inherit;
  text-decoration: none;
}

table {
  vertical-align: top;
}

img {
  max-width: 100%;
  vertical-align: top;
}

* {
  -webkit-tap-highlight-color: transparent;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.header_transparent {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

.header_white {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding: 0 0 13px;
  z-index: 111;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
  background-color: #fff;
}

.header_white .header__products ul li a {
  color: #131313;
}

.header.is-fixed {
  position: fixed;
}

.header__info {
  padding: 5px 0;
  background-color: #131313;
}

@media only screen and (max-width: 767px) {
  .header__info {
    padding: 6px 0;
  }
}

.header__info-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .header__nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    overflow: auto;
    opacity: 0;
    border: 0;
    padding: 0 0 60px;
    visibility: hidden;
    transform: translate3d(0, 100px, 0);
    transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
    background: #249bff;
    text-align: center;
  }
}

.header__nav.is-active {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0);
  transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease;
}

.header__nav > ul {
  display: flex;
}

@media only screen and (max-width: 767px) {
  .header__nav > ul {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: auto;
  }
}

.header__nav > ul li:not(:last-child) {
  margin-right: 29px;
}

@media only screen and (max-width: 1199px) {
  .header__nav > ul li:not(:last-child) {
    margin-right: 15px;
  }
}

@media only screen and (max-width: 767px) {
  .header__nav > ul li:not(:last-child) {
    margin: 0 0 20px;
  }
}

.header__nav > ul li a {
  font-size: 15px;
  line-height: 30px;
  color: #7a7a7a;
  transition: all 0.25s ease-out;
}

@media only screen and (max-width: 767px) {
  .header__nav > ul li a {
    color: #fff;
    font-family: "DINPro-Bold", Arial, sans-serif;
    font-weight: normal;
    text-transform: uppercase;
    font-size: 16px;
  }
  .header__nav > ul li a:hover {
    color: #131313;
  }
}

.header__nav > ul li a:hover {
  color: #fff;
}

.header__nav-btn {
  display: none;
  border: 0;
  outline: 0;
  padding: 0;
  margin-left: 30px;
  z-index: 11;
}

@media only screen and (max-width: 767px) {
  .header__nav-btn {
    display: block;
  }
}

.header__nav-btn.is-active span {
  color: #131313;
}

.header__nav-btn-burger {
  margin: auto;
  width: 40px;
  height: 48px;
  cursor: pointer;
}

.header__nav-btn-lines {
  width: 40px;
  height: 3px;
  position: relative;
  transform: translateY(25px);
  background: white;
  transition: all .3s;
}

.header__nav-btn-lines:after, .header__nav-btn-lines:before {
  width: 40px;
  height: 3px;
}

.header__nav-btn.is-active .header__nav-btn-lines {
  background: rgba(255, 255, 255, 0);
}

.header__nav-btn-lines:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 10px;
  background: white;
  transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn-lines:after {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  background: white;
  transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn.is-active .header__nav-btn-lines:after {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__nav-btn.is-active .header__nav-btn-lines:before {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.header__contact-tel {
  margin-right: 15px;
  font-family: "DINPro-Medium", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #fff;
  transition: all 0.25s ease-out;
}

@media only screen and (max-width: 767px) {
  .header__contact-tel {
    margin-right: 0;
    font-size: 15px;
  }
}

.header__contact-tel:hover {
  color: #249bff;
}

.header__contact-link {
  font-size: 15px;
  color: #249bff;
  border-bottom: 1px dotted currentColor;
  transition: all 0.25s ease-out;
}

@media only screen and (max-width: 767px) {
  .header__contact-link {
    display: none;
  }
}

.header__contact-link_mob {
  display: none;
  color: #fff;
  font-family: "DINPro-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  text-transform: uppercase;
  border-bottom: 0;
}

@media only screen and (max-width: 767px) {
  .header__contact-link_mob {
    display: inline-block;
    margin-top: 20px;
  }
}

.header__contact-link:hover {
  border-bottom-color: transparent;
}

.header__actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 27px 0 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

@media only screen and (max-width: 767px) {
  .header__actions {
    padding: 10px 0 15px;
    flex-wrap: wrap;
  }
}

.header__nav-btn_white .header__actions {
  border-bottom-color: #dbe1e6;
}

.header__logo {
  flex: 0 1 196px;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

@media only screen and (max-width: 767px) {
  .header__logo {
    flex: 0 0 108px;
  }
}

.header__search {
  flex: 0 1 625px;
  margin-right: -25px;
  padding: 0 20px;
}

@media only screen and (max-width: 1199px) {
  .header__search {
    margin-right: 0;
    padding: 0 30px;
  }
}

@media only screen and (max-width: 767px) {
  .header__search {
    order: 1;
    flex: 1 1 100%;
    padding: 0;
    margin-top: 15px;
  }
}

.header__products {
  padding: 15px 20px 0;
}

@media only screen and (max-width: 1199px) {
  .header__products {
    padding: 15px 0 0;
  }
}

@media only screen and (max-width: 767px) {
  .header__products {
    display: none;
  }
}

.header__products ul {
  display: flex;
  justify-content: space-between;
}

.header__products ul li a {
  font-family: "DINPro-Medium", Arial, sans-serif;
  font-weight: normal;
  line-height: 20px;
  color: #fff;
  transition: all 0.25s ease-out;
}

@media only screen and (max-width: 1199px) {
  .header__products ul li a {
    font-size: 12px;
  }
}

.header__products ul li a:hover {
  color: #249bff;
}

.header__basket {
  flex-shrink: 0;
  margin-right: 50px;
}

@media only screen and (max-width: 1199px) {
  .header__basket {
    margin-right: 0;
  }
}
